import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';
import { AllServicesService } from '../all-services.service';
import { NgForm } from '@angular/forms';
import { AuthService } from '../auth.service';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {

  constructor(private router: Router,
    private allService: AllServicesService,
    private authService: AuthService) { }

  ngOnInit(): void {
    if (this.router.url === '/login') {
      this.allService.disableTextbox();
    }
  }

  widthCondition: boolean;
  @ViewChild('form') createRoomForm: NgForm;
  showMessage: boolean = false
  message: string ='';

  // closePopup() {
  //   this.widthCondition = true;
  //   this.allService.enableTextbox();
  //   this.router.navigate(['/']);
  // }

  onLogin(form: NgForm) {
    const user = form.value.enterUser;
    const password = form.value.enterPassword;
    if(this.authService.login(user,password)) {
      this.message = 'Login Succesful !';
      this.showMessage = true;
      setTimeout(() => {
        this.showMessage = false;
        this.router.navigate(['/inventory']);
      }, 2000);
      
    } else {
      this.message = 'Login Failed !';
      this.showMessage = true;
      setTimeout(() => {
        this.showMessage = false
      }, 2000);
    }
    
  }

}
